@font-face {
  font-family: "Work Sans";
  font-weight: 400;
  src: url("../fonts/work-sans-regular.woff2") format("woff2"), url("../fonts/work-sans-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  font-weight: 600;
  src: url("../fonts/work-sans-600.woff2") format("woff2"), url("../fonts/work-sans-600.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  font-weight: 700;
  src: url("../fonts/work-sans-700.woff2") format("woff2"), url("../fonts/work-sans-700.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  margin: 0;
  background-color: #f2f2f2; }

a {
  text-decoration: none; }

img {
  width: 100%;
  height: auto; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }
